import axios from "axios";
import { endpoints } from "Constants/api";
import axiosHelpers from "Constants/axiosHelpers";
import {
  updateResidentTypes,
  updatePropertyTypes,
  setBasicDetails
} from "../redux/slice/basicSlice";

export const SignInAPI = async (data) => {
  const request = await axios({
    method: "post",
    url: endpoints.auth.SIGN_IN,
    headers: {
      Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data: data,
  }).catch(axiosHelpers);
  return request;
};

export const addBasicDetails = async (data, isPost) => {
  const request = await axios({
    method: isPost ? "post" : "put",
    url: endpoints.basicdetails.ADD_BASIC_DETAILS,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data: data,
  }).catch(axiosHelpers);
  return request;
};

export const getBasicDetails = (id) => async (dispatch) => {
  const request = await axios({
    method: "get",
    url: `${endpoints.basicdetails.GET_BASIC_DETAILS}/${id}`,
  }).then((res) => {
    dispatch(setBasicDetails(res.data.data))
  }).catch((e) => {
    axiosHelpers(e)
    dispatch(setBasicDetails(null))
  });
  return request;
}

