export const getPropertyTypes = () => async (dispatch) => {
  let request = await axios.get(endpoints.basicdetails.GET_PROPERTY_TYPES);
  dispatch(updatePropertyTypes(request.data));
};

export const getResidentTypes = () => async (dispatch) => {
  let request = await axios.get(endpoints.basicdetails.GET_RESIDENT_TYPES).catch(axiosHelpers);
  dispatch(updateResidentTypes(request.data));
  return request
};

